import React, { Suspense, useEffect, useContext } from 'react';
import { Provider } from 'react-redux';
import { PersistGate } from 'redux-persist/integration/react';
import CssBaseline from '@material-ui/core/CssBaseline';
import { MuiThemeProvider } from '@material-ui/core/styles';
import Div100vh from 'react-div-100vh';
import configStore from './configStore';
import InitWrapper from './InitWrapper';
import LoadingSpinner from './components/LoadingSpinner';

import AppRouter, { useRouter } from './router';
import theme from './theme';

import { SnackbarProvider } from 'notistack';
import AppErrorBoundary from 'react-error-boundary';
import { cleanStore } from 'localDB';
import { __RouterContext } from 'react-router';

const preventDefault = (e: any) => e.preventDefault();

const { store, persistor } = configStore();

const FallbackComponent: React.FC<{
  error?: Error;
  componentStack?: string;
}> = ({ error: Error, componentStack: string }) => {
  // TODO: Can say sorry to user, etc
  return <div>Something went wrong! Reloading in 5 seconds...</div>;
};

const App: React.FC = () => {
  useEffect(() => {
    window.addEventListener('touchmove', preventDefault, { passive: false });
    return () => window.removeEventListener('touchmove', preventDefault);
  }, []);

  const onError = async (error: Error, componentStack: string) => {
    // Clear all localstorage and store. Reload to refetch everything.
    await cleanStore('shops');
    await persistor.purge();
    setTimeout(() => {
      // Display Error message for 5 secs
      window.location.replace('/');
    }, 3000);
  };
  return (
    <>
      <CssBaseline />
      <MuiThemeProvider theme={theme}>
        <Provider store={store}>
          <PersistGate persistor={persistor}>
            <Suspense fallback={<LoadingSpinner />}>
              <SnackbarProvider
                maxSnack={2}
                anchorOrigin={{
                  vertical: 'top',
                  horizontal: 'right'
                }}
              >
                <Div100vh>
                  <AppErrorBoundary
                    onError={onError}
                    FallbackComponent={FallbackComponent}
                  >
                    <InitWrapper>
                      <AppRouter />
                    </InitWrapper>
                  </AppErrorBoundary>
                </Div100vh>
              </SnackbarProvider>
            </Suspense>
          </PersistGate>
        </Provider>
      </MuiThemeProvider>
    </>
  );
};

export default App;
